import React from "react";

import ListItemAnchor from "../../components/list-item/ListItemAnchor";
import { links } from "../../utils/links";

import "./Contact.scss";

export default function Contact() {
  return (
    <>
      <h3 id="contact" className="heading-3 content-wrapper__header">
        CONTACT
      </h3>

      <div className="contact__container">
        <div className="contact__links">
          {links.map((link) => (
            <ListItemAnchor linkData={link} />
          ))}
        </div>
      </div>
    </>
  );
}
