import Moon from "../../components/moon/Moon";
import Earth from "../../components/earth/Earth";
import { useTheme } from "../../context/theme-context/ThemeContext";

import "./Header.scss";

export default function Header() {
  const { theme } = useTheme();

  return (
    <header className="header">
      <div className="header__text">
        <h1 className={`heading-1 heading-1--${theme}`}>Hello!</h1>
        <h2 className="heading-2 mt-med">My name is Yusuf</h2>
        <p className="mt-med">
          I’m a{" "}
          <span className={`text--strong text--${theme}`}>
            web developer / student
          </span>{" "}
          located in Turkey. I love turning beautiful ideas into beautiful
          webproducts. I am deeply{" "}
          <span className={`text--strong text--${theme}`}>
            {" "}
            focused on front end development{" "}
          </span>
          and{" "}
          <span className={`text--strong text--${theme}`}>
            currently working on my design skills.
          </span>
        </p>
        <p className="mt-large">You can find about me more below! &darr;</p>
      </div>
      <Moon front={theme === "night"} />
      <Earth front={theme === "evening"} />
    </header>
  );
}
