import React from "react";

import ListItemAnchor from "../../components/list-item/ListItemAnchor";

import { projectList } from "../../utils/projects-list";

import "./Projects.scss";

function IndividualProject({ project }) {
  return (
    <>
      <div className="project">
        <div className="project__images">
          {project.images.map((imageLink, index) => (
            <figure
              className={`project__images-container project__images-container--${
                index + 1
              }`}
            >
              <a
                target="_blank"
                rel="noreferrer"
                href={imageLink}
                className="image__link"
              >
                <img src={imageLink} alt="" className="project__image " />
              </a>
            </figure>
          ))}
        </div>
        <div className="project__content">
          <h3 className="heading-3 project__header">{project.title}</h3>
          <div className="project__tag-container">
            {project.tags.map((tag) => (
              <span className="project__tag">{tag}</span>
            ))}
          </div>
          <p className="project__description">{project.text}</p>

          <div className="project__link-container">
            <ListItemAnchor
              linkData={{
                name: "Github",
                link: project.links.github,
                icon: "github",
              }}
            />
            <ListItemAnchor
              linkData={{
                name: "Demo",
                link: project.links.web,
                icon: "earth",
              }}
            />
          </div>
        </div>
      </div>
    </>
  );
}

export default function ProjectsList() {
  return (
    <>
      <h3 id="projects" className="heading-3 content-wrapper__header">
        PROJECTS
      </h3>

      <div className="projects__container">
        {projectList.map((project, index) => (
          <IndividualProject key={index + 1} project={project} />
        ))}
      </div>
    </>
  );
}
