import React from "react";

import "./Earth.scss";

export default function Earth({ front }) {
  return (
    <div className={`earth ${front ? "front" : ""}`}>
      <div className="earth__continent earth__continent--top"></div>
      <div className="earth__continent earth__continent--mid"></div>
      <div className="earth__continent earth__continent--bottom"></div>
    </div>
  );
}
