import React from "react";
import "./Star.scss";
export default function Star() {
  //Return random number between 1-maxInput
  function generateRandomNumber(maxInput) {
    return Math.floor(Math.random() * maxInput) + 1;
  }

  function randomStarGenerator(starCount) {
    let w = window.innerWidth;
    let h = document.documentElement.offsetHeight;

    let starts = [];
    for (let i = 0; i < starCount; i++) {
      let randomX = generateRandomNumber(w - 100);
      let randomY = generateRandomNumber(h);
      let randomSize = generateRandomNumber(5);

      starts.push(
        <i
          style={{
            left: `${randomX}px`,
            top: `${randomY}px`,
            width: `${randomSize}px`,
            height: `${randomSize}px`,
          }}
        />
      );
    }
    return starts;
  }

  return <>{randomStarGenerator(40)}</>;
}
