import React from "react";

import { useTheme } from "../../context/theme-context/ThemeContext";
import iconSprite from "../../assets/img/icon-svg/icon-sprites.svg";

import "./ListItem.scss";
export default function ListItem({ skillData }) {
  const { theme } = useTheme();
  return (
    <li className={`list__item list__item--${theme}`}>
      <svg className="list__icon">
        <use href={`${iconSprite}#icon-${skillData.icon}`}></use>
      </svg>
      <p className="list__text">{skillData.name}</p>
    </li>
  );
}
