import { useEffect, useRef, useState } from "react";

import "./Canvas.scss";

const Canvas = (props) => {
  const canvasRef = useRef(null);

  const [canvasSize, setCanvasSize] = useState({
    canvasW: window.innerWidth,
    canvasH: document.documentElement.offsetHeight,
  });

  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext("2d");

    setCanvasSize({
      canvasW: window.innerWidth,
      canvasH: document.documentElement.offsetHeight,
    });

    randomStarGenerator(150, context);
  }, [document.documentElement.offsetHeight]);

  function generateRandomNumber(maxInput) {
    return Math.floor(Math.random() * maxInput) + 1;
  }

  function randomStarGenerator(starCount, context) {
    const randomStars = [];
    for (let i = 0; i < starCount; i++) {
      let randomX = generateRandomNumber(canvasSize.canvasW - 100);
      let randomY = generateRandomNumber(canvasSize.canvasH);
      let randomSize = generateRandomNumber(5);

      randomStars.push([randomX, randomY, randomSize, randomSize]);
      context.fillStyle = "#FFF";
      context.fillRect(randomX, randomY, randomSize, randomSize);
    }
  }

  return (
    <>
      <canvas
        width={canvasSize.canvasW}
        height={canvasSize.canvasH}
        className="canvas__stars"
        ref={canvasRef}
        {...props}
      />
    </>
  );
};

export default Canvas;
