import React from "react";

import { useTheme } from "../../context/theme-context/ThemeContext";
import iconSprite from "../../assets/img/icon-svg/icon-sprites.svg";

import "./ListItem.scss";
export default function ListItemAnchor({ linkData }) {
  const { theme } = useTheme();
  return (
    <a
      target="-blank"
      href={linkData.link}
      className={`list__item list__item--${theme} list__link`}
    >
      <svg className="list__icon">
        <use href={`${iconSprite}#icon-${linkData.icon}`}></use>
      </svg>
      <p className="list__text">{linkData.name}</p>
    </a>
  );
}
