import React from "react";

import "./Moon.scss";

export default function Moon({ front }) {
  return (
    <div className={`moon ${front ? "front" : ""}`}>
      <div className="moon__crater moon__crater--top"></div>
      <div className="moon__crater moon__crater--mid"></div>
      <div className="moon__crater moon__crater--bottom"></div>
    </div>
  );
}
