import React from "react";

import "./Skills.scss";

import { skillsList } from "../../utils/skills-list";
import ListItem from "../../components/list-item/ListItem";

export default function Skills() {
  return (
    <>
      <h3 id="skills" className="heading-3 content-wrapper__header">
        SKILLS
      </h3>

      <div className="skills__container">
        {skillsList.map((skillCategory) => (
          <div className="skills__category">
            <h4 className="skills__header  mb-small">{skillCategory.title}</h4>
            <ul className="skills__list">
              {skillCategory.list.map((skill, index) => (
                <ListItem key={index + skill.name} skillData={skill} />
              ))}
            </ul>
          </div>
        ))}
      </div>
    </>
  );
}
